.pricing{
width: 100%;
height: 80vh;
}
.card-container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px auto;
    padding: 10px 50px;
}
.card{
  border: 1px solid rgb(236, 238, 234);
  text-align: center;
    padding: 30px 40px;
}
.card-item{
    margin-bottom: 22px;

}
.card h3{
line-height: 30px;
margin-bottom: 20px;
}
.card p{
line-height: 30px;
margin-bottom: 10px;
letter-spacing: 1px;
}

@media screen and (max-width:640px){
    .pricing{
        height: 100%;
    }
    .card-container{ 
       flex-direction: column;
        justify-content: space-between;
        margin: 10px auto;
        padding: 10px 50px;
    }
    .card{
        margin: 10px 0;
    }
    .card p{
        margin-bottom: 0;
    }
}