.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgb(15, 16, 16);
}
.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
    padding: 0 10rem;
}
.left{
    height: 100%;
    max-width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}
.right{
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}
.social{
    margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
@media screen  and (max-width:640px){
    .footer-container{
        grid-template-columns: 1fr;
        padding: 0 1rem;
        text-align: center;
    }
    .location{
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
@media screen and (max-width:360px){
    h4{
        font-size: 10px;
    }
    h3{
        font-size: 10px;
    }
    p{
        font-size: 10px;
    }
}