.contact-form{ 
    width: 100%;
    height: 100vh;
}
.form{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px auto;
}
form{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
form label{
margin-top: 1.2rem;
margin-bottom: 1rem;
font-size: 1rem;
}
form input{
padding: 5px 5px;
font-size: 20px;
border: 2px solid rgb(117, 115, 115);
border-radius: 3px;
outline: none;
background-color: rgb(44, 42, 42);
color: white;
}
form textarea{
margin-top: 1rem;
font-size: 20px;
padding: 10px 10px;
border: 2px solid rgb(94, 93, 93);
border-radius: 3px;
outline: none;
background-color: rgb(44, 42, 42);
color: white;
}
.submit{
    width: 30%;
    font-size: 1.3rem;
    text-align: center;
    background-color: yellow;
    border: 2px solid rgb(91, 88, 88);
    outline: none;
    padding: 5px 0;
    margin: 20px auto;
    cursor: pointer;
    font-weight: 500;
}
.submit:hover{
    background-color: rgb(75, 74, 74);
    color: white;
}

