@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');
*{
    font-family:  "Outfit", sans-serif;
}
.hero-img {
    width: 100%;
    /* background-color: rgb(100, 96, 96); */
    height: 100vh;
    position: relative;
}

.hero-img::before {
    content: "";
    background: url("../Images/bgImage4.jpg");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.5;
}
.hero-img h1{
    font-size: 2.4rem;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.abouttext{
    text-align: center;
}
@media screen and (max-width:640px) {
    .hero-img h1{
        font-size:2rem ;
    }
    .abouttext{
        margin: 0 auto;
        max-width: 500px;
        padding: 0 20px;
        text-align: center;
    }
}
@media  screen and (max-width:360px){
.hero-img h1{
font-size: 15px;
}
.hero-img p{
    font-size: 12px;   
}
.abouttext{
    width: 300px;
    text-align: center;
}
}