* {
    box-sizing: border-box;
}

.workcard {
    width: 100%;
    height: 100%;
    background: rgb(16, 16, 16);
}

.mainCard {
    padding: 10px;
}


.workcard .card-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.project-cards img {
    height: 146px;
    width: 250px;
    /* margin: auto; */
}

.project-cards {
    border: 1px solid white;
    /* text-align: center; */
    padding: 10px 10px;
    text-align: center;
}

.project-cards h2 {
    font-size: 20px;
}

.pro-details p {
    width: 250px;
    margin: 10px auto;
    font-size: 15px;
}

.pro-btns {
    display: flex;
    justify-content: space-between;
}

.pro-btns .btn {
    padding: 3px 10px;
    font-size: 15px;
}

@media screen and (max-width:640px) {
    .workcard .card-container {
        display: flex;
        flex-direction: column;

    }
}

@media screen and (max-width:360px) {
    .workcard .card-container {
        flex-wrap: nowrap;
    }
}