.mask{
    width: 100%;
    height: 100vh;
    position: relative;

}
.bg-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.6;
}
.homebtn{
    display: flex;
    justify-content: center;
    align-items: center;
}
.hero{
    width: 100%;
    height: 100%;

}
.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1{
font-size: 3rem;
padding: 0.6rem 0 1.5rem;

}
.hero .content p{
    font-size: 2rem;
    font-weight: 500;
    text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
    max-width: fit-content;
}

@media  screen and (max-width:640px) {
    .hero .content h1{
        font-size: 1.5rem;
    }
    .hero .content p{
        font-size: 0.8rem;
        margin-top: -100px;
    }
    .hero .content{
        top:80%;
    }
    .homebtn{
        display: flex;
        flex-direction: row;
    }
}
