*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 60px;
    z-index: 10;
}
.header-bg{
    background: black;
    transition: 0.5s;
}
.nav-menu{
    /* margin-right: 50px; */
    margin: auto;
}
.nav-item{
    display: flex;
}

.nav h1{
    margin-left: 50px;
    margin-top: 5px;
}
.hamburger{
    display: none;
}
@media screen and (max-width:1040px) {
    .nav-item{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgb(7, 7, 7);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.2s;
    }
    .nav-item.active{
        left: 0;
    }
    .nav-item li{
        padding: 0.5rem 0;
    }
    .nav-item a{
        font-size: 1.5rem;
    }
    .hamburger{
        display: initial;
        margin-right: 5px;
    }
}