.about-item{

    box-sizing: border-box;
}
.about{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.about .left{
    max-width: 30%;
    text-align: center;
    padding: 1rem;
    margin: auto;
}
.about .left p{
    margin-top:1.5rem; 
    margin-bottom: 2rem;
}
.about .right{
    max-width: 700px;
}
.right .img-container{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
}
.right img{
   max-width: 90%;
}
.right .img-stack-top{
    grid-column: 1/span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.right .img-stack-bottom{
    grid-column: 4 /-1;
    grid-row: 1;
    z-index: 5;
}
.eduabout{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 40px auto;
}
.eduleft{
flex-basis: 75%;
margin-bottom: 50px;
}
.eduright{
flex-basis: 80%;
}
.edudetails1{
    margin-top: 20px;
    line-height: 30px;
    letter-spacing: 1px;
}
